<template>
    <div class="login">
        <form action="#" name="myForm">
            <div class="login-text" data-msg="用户名不合法，4到16位（字母，数字，下划线，减号）">
            <i class="iconfont icon-phone"></i>
            <input type="text" placeholder="请输入你的用户名" class="username">
        </div>
        <div class="login-text" data-msg="密码不合法，4到16位（字母，数字，下划线，减号）">
            <i class="iconfont icon-password1"></i>
            <input type="password" placeholder="请输入你的密码" class="password">
        </div>
        <button class="login-btn" @click="loginBtn">登陆</button>
        </form>
    </div>
</template>
<script>
    export default{
        name:"login",
        data(){
            return{
                tagname:""
            }
        },
       methods:{
            loginBtn(){
                  setTimeout(() => {
                     if(sessionStorage.isLogin){
                       this.$router.push({path:'/person-center'});
                     }
            }, 1000);
                    }
        },
        mounted(){
        $(function(){
            let _isLogin = true;
        // 3. 表单验证
        $('.login-text input').on('input', function () {
            let type = this.className;
            if (Validate.test(type, $(this).val())) {
                // 合法
                $(this).parent().removeClass('error');
            } else {
                // 不合法
                $(this).parent().addClass('error');
            }
        });
        // 4. 点击登陆注册按钮
        $('.login-btn').on('click', function () {
            let isThrough = true;
            let $inputs =  _isLogin ? $(".login-text:lt(2) input") : $(".login-text input") ;
            // 表单异常处理
            for (let i = 0, len = $inputs.length; i < len; i++) {
                if (!$inputs.eq(i).val() || $inputs.eq(i).parent().hasClass('error')) {
                    isThrough = false;
                    break;
                }
            }
            if (!isThrough) {
                alert("信息为空或者不合法")
                return;
            }
            // 注册Bmob服务
            Bmob.initialize("44d02198809694a29dca95912ce066eb", "149f5b3499ab2f3357eb27bf42d5a899");
            if (_isLogin) {
                // 登陆
                Bmob.User.login($('.username').val(),$('.password').val()).then(res => {
                   sessionStorage.isLogin = true;
                   alert("登陆成功");
                }).catch(err => {
                   alert(err.error);
                });
            } else {
                // 创建用户对象
                let user = {};
                $inputs.each((index, input) => {
                    let key = $(input).attr('class');
                    let val = $(input).val();
                    user[key] = val;
                });
                // 调用BMOB注册用户
                Bmob.User.register(user).then(res => {
                    sessionStorage.isLogin = true;
                    alert("注册成功");
                }).catch(err => {
                    alert(err.error);
                });
            }
        });
});
class Validate {
    constructor() {}
    static test(type, str) {
        let res = null;
        switch (type) {
            case "username": {
                // 1. 用户名正则，4到16位（字母，数字，下划线，减号）
                res = /^[A-Za-z0-9_-]{4,16}$/.test(str);
            } break;
            case "password": {
                // 2. 用户名正则，4到16位（字母，数字，下划线，减号）
                res = /^[A-Za-z0-9_-]{4,16}$/.test(str);
            } break;
        }
        return res;
    }
 }
 }

    }
</script>
<style scoped>

</style>




