<template>
    <div class="shop-cart page">
        <section class="check-box box-page">
                                <div class="all-check check-box">
                                    <span class="check-item" ></span>
                                    <span class="check-tips">全选</span>
                                </div>
                                <div>
                                    <span class="delete">删除选中</span>
                                    <span class="edit">编辑</span>
                                </div>
        </section>
       <ul class="book-shop">
           <li v-for="(item, index) in datas" :key="index" class="check-box single-box">
               <span class="check-item" ></span>
               <img :src="item.img" alt="">
               <div class="all-data">
                   <p class="data-name">{{item.name}}</p>
                   <p class="data-author">{{item.author}}</p>
                   <p class="data-price">单价：￥{{item.price}}</p>
                   <div class="all-cart">
                       <span class="or-total">总价：￥{{item.total}}</span>
                       <div class="cart">
                    <span class="down">-</span>
                    <input type="text" :value="item.num" class="input">
                    <span class="up">+</span>
                       </div>
                   </div>
               </div>
           </li>
       </ul>
       <div class="footer-num">
           <div class="all-check check-box">
                                    <span class="check-item" ></span>
                                    <span class="check-tips">全选</span>
            </div>
            <div>
                <p class="left-total"><span>合计(不含运费):</span>
                <span class="big-color">￥<span class="total-price">0.00</span></span></p>
                <p class="left-total"><span>总额:</span>
                <span class="big-color">￥<span class="total-price">0.00</span></span></p>
            </div>
            <div>
                <button class="settlement" @click="set">去结算(<span class="singleton">0</span>)</button>
            </div>
       </div>
    </div>
</template>
<script>
export default {
    name:"shop",
    data(){
        return{
            rootArr:'',
            datas:null,
            ordershow:false,
            order:null
        }
    },
    created(){
       // 1.读取购物车
         if(localStorage.orders && JSON.parse(localStorage.orders).length > 0){
             this.datas=JSON.parse(localStorage.orders);
            
         }
         else{
             $(".book-shop").html("<p class='no-order'>o(╥﹏╥)o购物车什么都没有，去逛逛？~~~</p>");
         }
    },
    methods:{
        set(){
                if(this.$store.state.isLogin){
                    if( $(".settlement").hasClass("check-color")){
                        this.$router.push({path:'/settlement'});
                    }else{
                         alert("请先选择商品才能结算")
                    }
                }else{
                    alert("请先登陆")
                }
           
        }
    },
    mounted(){
         let selectedIndexs = []; // 记录选中下标
         let editSelectedIdnexs = []; // 记录编辑状态下选中下标
         let isEditing = false; // 记录编辑状态
         
        let datas=this.datas;
        $('.check-item').click(function() { 
        // 获取check元素
        let $checkItem = $(this);
        // 切换check元素状态
        $checkItem.toggleClass('checked');
        if(!isEditing) {
            $checkItem.hasClass('checked') ? $checkItem.data('checked', true) : $checkItem.data('checked', false);
        }
        // 获取购物车数量长度
        let len = $(".single-box .check-item").length;
        // 判断是点击全选还是单独选
        if($(this).parent().hasClass('all-check')) { // 全选
            if($checkItem.hasClass('checked')) { // 选中
                // 更新下标
                for(let i = 0; i < len; i++) {
                    isEditing ? editSelectedIdnexs[i] = i : selectedIndexs[i] = i;
                }
                $(".check-box .check-item").addClass('checked')
            }else { // 未选中
                // 如果全选按钮为选中，则根据编辑状态清空下标集合
                isEditing ? editSelectedIdnexs = [] : selectedIndexs = [];
                $(".check-box .check-item").removeClass('checked');
            }
            if(!isEditing) {
                updateFoot();
                $(".check-box .check-item").data('checked', $checkItem.hasClass('checked'));
            }
        }else { // 单选
            // 获取下标
            let index = $checkItem.parents('.single-box').index();
            if($checkItem.hasClass('checked')) { // 选中
                // 加入选中下标
                isEditing ? editSelectedIdnexs.push(index) : selectedIndexs.push(index);
                // 如果单选按钮的长度和记录选中下标集合的长度一致，则让全选按钮呈现选中状态
                if(isEditing && len == editSelectedIdnexs.length) {
                    $('.all-check .check-item').addClass('checked')
                }else if(!isEditing && len == selectedIndexs.length) {
                    $('.all-check .check-item').addClass('checked')
                }
            }else { // 未选中
                // 删除选中下标
                isEditing ? editSelectedIdnexs.splice(editSelectedIdnexs.indexOf(index), 1) : selectedIndexs.splice(selectedIndexs.indexOf(index), 1)
                // 去除全选选中状态
                $('.all-check .check-item').removeClass('checked');
            } 
            !isEditing && updateFoot();
        }
        });
            // 3.处理编辑
        $('.edit').click(function() {
            // 更新编辑状态
            isEditing = !isEditing;
            // 更新标题
            $(this).text(isEditing ? "取消" : "编辑");
            if($(this).text()==="取消"){
                $(".edit").css({
                    "color":"red"
                });
                $(".delete").css({
                "display":"inline-block"
            });
            }else{
                $(".edit").css({
                    "color":"#00c3f5"
                });
                $(".delete").css({
                "display":"none"
            })
            }
            // 根据编辑状态切换显示
            if(isEditing) {
                $('.check-item').removeClass('checked');
                $(".settlement").removeClass("check-color");
            }else {
                
                // 重置非编辑状态下的选中下标集合
                editSelectedIdnexs = [];
                // 如果在编辑前选中，则修改选中状态
                $('.check-item').each((index, item) => {
                    if($(item).data('checked') == true) {
                        $(item).addClass("checked");
                    }else {
                        $(item).removeClass("checked");
                    }
                });
                if($('.single-box .check-item').length == selectedIndexs.length) {
                    $('.all-check .check-item').addClass('checked');
                }else {
                    $('.all-check .check-item').removeClass('checked');
                }
                // 判断是否还有数据
                if($('.single-box').length == 0) {
                    $(".book-shop").html("<p class='no-order'>o(╥﹏╥)o购物车什么都没有，去逛逛？~~~</p>");
                    $(".box-page").css({
                        opacity:"0"
                    });
                    $(".footer-num").css({
                        opacity:"0"
                    })
                    $('.all-check .check-item').data('checked', false);
                    $('.all-check .check-item').removeClass('checked');
                }
                // 重置选中下标
                selectedIndexs = [];
                $(".single-box .check-item").each((index,item) => {
                    if($(item).data('checked') == true ) {
                        selectedIndexs.push(index);
                    } 
                });
                updateFoot();
                
            }
        });
                // 4. 处理‘+’
        $('.up').click(function() {
            // 4.1. 更新输入框的数量值
            let $input = $(this).siblings('.input');
            $input.val(parseInt($input.val()) + 1);
            // 4.2. 修改购物车订单参数
            updateOrder(this);
            updateFoot();
        });
        // 5. 处理‘-’
        $('.down').click(function() {
        // 5.1. 更新输入框的数量值
        let $input = $(this).siblings('.input');
        if($input.val() === '1') {
            return;
        }
        $input.val(parseInt($input.val()) - 1);
        // 5.2. 修改购物车订单参数
        updateOrder(this);
        updateFoot();
            

        });
        // 7. 处理删除
        $('.delete').click(function() {
            let $orders = $('.single-box');
            editSelectedIdnexs.forEach(index => {
                // 删除页面元素
                $orders.eq(index).remove();
                datas.splice(index, 1);
                // 删除非编辑状态下的选中下标
                let idx = selectedIndexs.indexOf(index);
                if(idx != -1) {
                    selectedIndexs.splice(idx, 1);
                }
            });
            // 更新全选状态
            $('.all-check .check-item').removeClass('checked');
            // 删除本地购物车数据
            remove(editSelectedIdnexs);
            //实时更新购物车数量
            $(function(){
                let rootArr = JSON.parse(localStorage.orders);
                $('.circle em').text(rootArr ? rootArr.length : 0);
            })
        });
        // 6. 处理输入框
        $('.input').change(function() {
            // 6.1. 异常处理,数量不能小于1
            if($(this).val() < 1) {
                $(this).val(1);
            }
            // 6.2. 修改购物车订单参数
            updateOrder(this);
            updateFoot();
        });
        function updateFoot() {
            // 更新去结算购物车订单数量
            $('.settlement').text(`去结算(${selectedIndexs.length})`);
            $('.singleton').text(`${selectedIndexs.length}`);
            if($(".check-item").hasClass("checked")){
                $('.settlement').addClass("check-color");
            }else{
                $('.settlement').removeClass("check-color");
            }
            //更新结算总结
            let total = 0;
            selectedIndexs.forEach(index => {
                total += parseFloat(datas[index].total);
            });
            $('.total-price').text(`${total.toFixed(2)}`);
            let order=total.toFixed(2);
            sessionStorage.price=order;
        }
   
    // 更新购物车每一个订单的数据变化
    function updateOrder(tar) {
        // 获取li
        let $order  = $(tar).parents('.single-box');
        // 获取li所在的下标
        let index   = $order.index();
        // 根据下标获取购物车数据
        let order   = datas[index];
        // 修改当前订单数据
        order.num    = parseInt($order.find('.input').val());
        order.total  = (order.num * order.price).toFixed(2);
        // // 修改本地购物车存储的数据
        modify(index, order);
        // 修改页面价格显示
        $order.find('.or-total').text("总价："+"￥"+order.total);
    }
 
    function modify(index, order) {
        // 获取购物车订单
        let orders = JSON.parse(localStorage.orders);
        // 修改对应下标的订单
        orders[index] = order;
        // 更新本地购物车
        localStorage.orders = JSON.stringify(orders);
    }
     function remove(indexs) {
        // 获取购物车订单
        let orders = JSON.parse(localStorage.orders);
        let tmpArr = [];
        // 遍历购物车所有数据
        for(let i = 0, len1 = orders.length; i < len1; i++) {
            // 记录是否要将不删除的购物车数据存放至临时数组中
            let flag = false;
            // 遍历删除下标
            for(let j = 0, len2 = indexs.length; j < len2; j++) {
                // 判断购物车数据的下标和要删除的数据的下标是否匹配
                // 如果匹配，则表示这个数据要被删除
                // 就不用添加到临时数组中
                if(i == indexs[j]) {
                    flag = true;
                }
            }
            // 如果该数据下标没有对应要删除的数据下标，
            // 那么就将该数据存储到临时数组中即可
            // 整个循环结束之后，临时数据集合里面的数据就都是保留下来的数据了
            if(!flag) {
                tmpArr.push(orders[i]);
            }
        }
        // 更新本地购物车
        localStorage.orders = JSON.stringify(tmpArr);
    }
    // 更新无货的时候的状态
    $(function(){
         if($('.single-box').length == 0) {
                $(".book-shop").html("<p class='no-order'>o(╥﹏╥)o购物车什么都没有，去逛逛？~~~</p>");
                $(".box-page").css({
                    opacity:"0"
                });
                $(".footer-num").css({
                    opacity:"0"
                })
    }
    })
    },
    watch:{
       datas(){
           this.datas=JSON.parse(localStorage.orders);
           return this.datas
       }
    }
}
</script>
<style scoped>
@font-face {
    font-family: 'iconfont';  /* project id 929459 */
    src: url('//at.alicdn.com/t/font_929459_qc07uj1dgk9.eot');
    src: url('//at.alicdn.com/t/font_929459_qc07uj1dgk9.eot?#iefix') format('embedded-opentype'),
    url('//at.alicdn.com/t/font_929459_qc07uj1dgk9.woff') format('woff'),
    url('//at.alicdn.com/t/font_929459_qc07uj1dgk9.ttf') format('truetype'),
    url('//at.alicdn.com/t/font_929459_qc07uj1dgk9.svg#iconfont') format('svg');
  }
.iconfont{
    font-family:"iconfont" !important;
    font-size:16px;font-style:normal;
    -webkit-font-smoothing: antialiased;
    -webkit-text-stroke-width: 0.2px;
    -moz-osx-font-smoothing: grayscale;}
.shop-cart{
    background: #F2F2F2;
}
.box-page{
    padding: 10px 5px;
    text-align: left;
    display: flex;
    font-size: 14px;
    justify-content: space-between;
}
.delete{
    display: none;
    color: rgb(236, 4, 4);
    margin-right: 8px;
}
.edit{
    color: #03689A;
}
.book-shop{
    padding-bottom: 50px;
}
.book-shop li{
    display: flex;
    padding: 10px 5px;
    align-items: center;
    background: #fff;
    border-bottom: 1px solid #d3d3d3;
}
.book-shop li img{
    width: 75px;
    height: 90px;
    margin-right: 10px;
}
.check-item{
        display: inline-block;
        margin-right: 5px;
        vertical-align: middle;
        width: 15px;
        height: 15px;
        border: 1px solid #BDBDBD;
        border-radius: 50%;
        background-color: #fff;
        text-align: center;
        font-size: 10px;
        line-height: 17px;
        cursor: pointer;
        -webkit-user-select: none;
        -moz-user-select: none;
        -ms-user-select: none;
        user-select: none;
}
.check-item::before{
        font-family:"iconfont" !important;
        font-size:16px;font-style:normal;
        -webkit-font-smoothing: antialiased;
        -webkit-text-stroke-width: 0.2px;
        -moz-osx-font-smoothing: grayscale;
        content: "\e60e";
        color: #fff;
    }
.checked{
        border-color: #00c3f5;
        background-color: #00c3f5;
    }
.all-data{
    text-align: left;
    width: 200px;
}
.all-cart{
    display: flex;
    justify-content: space-between;
}
.cart{
    font-size: 0;
    text-align: center;
}
.cart span{
    width: 24px;
    height: 19px;
    font-size: 14px;
    border: 1px solid #d3d3d3;
    display: inline-block;
    box-sizing: border-box;
}
.input{
    width: 20px;
    text-align: center;
}
.data-name{
    font-size: 15px;
    color: #6EB4ED;
    overflow: hidden;
    white-space: nowrap;
    text-overflow: ellipsis;
}
.data-author{
    font-size: 14px;
    color: rgb(169, 204, 87);
}
.data-price{
    font-size: 13px;
    color: rgb(145, 80, 80);
}
.all-cart>span{
    font-size: 13px;
    color: #d42b26
}
.footer-num{
    display: flex;
    width: 100%;
    font-size: 13px;
    padding: 0 0 0 5px;
    box-sizing: border-box;
    bottom: 50px;
    justify-content:space-between;
    align-items: center;
    height: 45px;
    position: fixed;
    background: #fff;
    border-top: 1px solid #d3d3d3;
     border-bottom: 1px solid #d3d3d3;
}
.left-total{
    text-align: left;
}
.settlement{
    height: 45px;
    padding: 0 10px;
}
.check-color{
        background-color: rgb(233, 28, 52);
        border: 1px solid  rgb(233, 28, 52);
        color: #fff;
}
.book-shop p{
    
}
</style>





