<template>
    <div class="class-book">
         <i class="iconfont icon-Left" @click="callback" ref="showi"></i>
        <div class="btn">
           <input type="text" placeholder="请输入你想查找的图书名称" v-model="search">
           <i class="iconfont icon-search" @click="searchBtn"></i>
        </div>
        <ul class="all-book" ref="hidbook">
            <li v-for="(item, index) in msg" :key="index" @click="Details(item.name)">
                <img :src="item.cover" alt="">
                <div class="msg">
                    <p class="msg-name">{{item.name}}</p>
                    <p class="msg-author">{{item.author}}
                        <i class="iconfont icon-right"></i>
                    </p>
                    <p class="msg-price">￥{{item.price}}</p>
                </div>
            </li>
        </ul>
        <p @click="getMore" class="more" v-show="ishid">查看更多</p>
        <p v-show="isshow">已加载全部</p>
    </div>
</template>
<script>
export default {
    name:"class-book",
    data(){
        return{
            msg:'',
            search:'',
            arr:null,
            num:5,
            isshow:false,
            ishid:true
        }
    },
     created() {
        this.$axios.get('./static/json/book-info.json', {
        params: {
            // get 参数可放置于此字段中，key-value对形式设置
            // 一般建议这么做，便于控制
        }
        }).then(res => {
        let ress=res.data.slice(0,this.num);
        this.arr=ress;
        this.msg=ress;
        })
        .catch(error => {
        console.log(error);
        })
},
methods:{
        getMore(){
            this.num=this.num+6;
            this.$axios.get('./static/json/book-info.json', {
        params: {
            // get 参数可放置于此字段中，key-value对形式设置
            // 一般建议这么做，便于控制
        }
        }).then(res => {
        let ress=res.data.slice(0,this.num);
        this.arr=ress;
        this.msg=ress;
        if(this.arr.length==res.data.length){
            this.isshow=true;
            this.ishid=false;
        }
        })
        .catch(error => {
        console.log(error);
        })
        },
        Details(item){
            this.inx=item;         
        this.$router.push({path:'/details',query:{goods:this.inx}});
        },
        searchBtn(){
            this.$refs.showi.style.display="block";
            let search=new RegExp(this.search,'i');
           if(search){
                this.msg=this.msg.filter(function(book){
                return search.test(JSON.stringify(book));
            })
           }
           if(this.msg.length==0){
               this.$refs.hidbook.innerHTML="<p class='nobook'>暂无图书信息</p>"
           }
        },
        callback(){
            this.$router.go(-1);
        }

}
}

</script>
<style scoped>
.class-book>i{
    position: absolute;
    z-index: 100;
    font-size: 25px;
    color: #ffffff;
    top: 7px;
    left:5px;
    display: none;
}
.btn{
    height: 33px;
    padding-bottom: 0px;
    position: relative;
}
.btn i{
    position: absolute;
    left:8px;
    top:7px;
}
.btn input{
    width: 100%;
    height: 30px;
    border: 1px solid #d3d3d3;
    outline: none;
    padding: 10px 15px;
    padding-left: 25px;
    box-sizing: border-box;
    border-radius: 15px;
}
.class-book{
    padding: 10px 15px;
}
.class-book>p{
    font-size: 12px;
    margin-top: 10px;
}
.all-book li{
    display: flex;
    justify-content: flex-start;
    border-bottom:1px solid #dddcdc;
    padding-top: 10px;
    padding-bottom: 15px;
}
.all-book li img{
    width: 90px;
    height: 100px;
}
.msg{
    text-align: left;
    width: 77%;
    padding: 15px;
    box-sizing: border-box;
    
}
.msg p{
    width: 100%;
    font-size: 14px;
    margin-bottom: 5px;
}
.msg-name{
    
    color: rgb(135, 231, 199);
}
.msg-author{
    color: darkgrey;
}
.msg-author i{
    float: right;
}
.msg-price{
    color: rgb(187, 30, 30);
    font-size: 17px;
}

</style>





