<template>
    <div class="person-center page">
        <div class="person-img">
            <div class="small-img">
                <img src="https://goss.veer.com/creative/vcg/veer/800water/veer-153102845.jpg" alt="">
               
            </div>
             <p class="auto">{{this.$store.state.tagName.username}}</p>
        </div>
        <ul class="msg-list">
            <li v-for="(item, index) in msgs" :key="index" @click="dataPerson(item,index)">
                {{item.name}}
                <i class="iconfont icon-right"></i>
            </li>
        </ul>
        <button class="sign-out" @click="signout">退出登陆</button>
    </div> 
</template>
<script>
    export default{
        name:"personCenter",
        data(){
            return{
                msgs:[
                    {name:"设置个人资料",url:"/person-data"},
                    {name:"收货地址管理",url:""},
                    {name:"我的订单",url:""},
                    {name:"我的评价",url:""},
                    {name:"商品收藏",url:""},
                    {name:"店铺收藏",url:""},
                    {name:"浏览记录",url:""},
                ]
            }
        },
        methods:{
            signout(){
                sessionStorage.isLogin=false;
                sessionStorage.clear();
                this.$router.push({path:'/mine-center/login'});
            },
            dataPerson(item,index){
                this.$router.push({path:item.url})
            }
        }
       
    }
</script>
<style scoped>
.person-center{
    background: #e7e7e7;
}
.person-img{
    width: 100%;
    height: 150px;
    background: url("https://goss.veer.com/creative/vcg/veer/800water/veer-153102845.jpg") center center / cover;
    padding-top: 20px;
    box-sizing: border-box;
    margin-bottom: 10px;
}
.person-img>p{
    color: rgb(33, 152, 231);
}

.small-img{
    width: 90px;
    height: 90px;
    border-radius: 50%;
    overflow: hidden;
    margin: 0 auto;
}
.small-img img{
    width: 100%;
    height: 100%;
}
.msg-list{
    text-align: left;
}
.msg-list li{
    padding: 13px 15px;
    font-size: 13px;
    display: flex;
    justify-content: space-between;
    background: #ffffff;
    border-bottom: 1px solid #d3d3d3;
}
.sign-out{
    margin-top: 25px;
    width: 98%;
    padding: 10px;
    margin-bottom: 40px;
    border: none;
    outline: none;
    border-radius: 5px;
    letter-spacing: 5px;
    color: #ffffff;
    background: #FF3333;
}
</style>




