<template>
  <div id="app">
    <div class="header">
      <h1>{{this.$store.state.title}}</h1>
    </div>
    <router-view/>
    <transition>
    <ul class="ul-list" >
      <li v-for="(item, index) in links" :key="index">
        <i :class=" item.icon"></i>
        <router-link :to='item.path'>{{item.title}}</router-link>
      </li>
    </ul>
    </transition>
    <div class="circle">
      <em>{{num?num:0}}</em>
    </div>
    <div class="hidshow">
     <button @click="hidshow">跳过广告(<span class="settime">3</span>)</button>
    </div>
  </div>
</template>

<script>
export default {
  name: 'App',
  data(){
   
   return{
      links:[
      {
        path:'/',
        title:"首页",
        icon:"iconfont icon-home "
      },
      {
        path:'/classify',
        title:"分类",
         icon:"iconfont icon-svgclassify"
      },
      {
        path:'/shop-cart',
        title:"购物车",
         icon:"iconfont icon-cart"
      },
      {
        path:'/mine-center',
        title:"我的",
         icon:"iconfont icon-mine"
      }
    ]
   }
  },
   mounted(){
     this.$store.commit('changeshow',true);
     setTimeout(() => {
       $(".hidshow").css({
         "display":"none"
       })
     }, 3000);
     $(function(){
       let wait=$(".settime").html();
       if(wait>0){
         let t=setInterval(() =>{
           $(".settime").text(--wait)
          if(wait==0){
           clearInterval(t)
         }
         },1000);
       }
     });
    //  改变底部颜色变换
     $(".ul-list li").on("click",function(){
      let index=$(this).index();
     $(this).find("a,i").css({
        color:"rgb(52, 179, 136)"
      }).parent().siblings().find("a,i").css({
        color:"#2c3e50"
      })
     });
   },
   methods:{
     hidshow(){
       $(".hidshow").css({
         "display":"none"
       });
       this.$store.commit('changeshow',true);
     }
   },
   computed:{
     num(){
       if(localStorage.orders){
         let num=JSON.parse(localStorage.orders);
         return num.length
       }
     }
   },
   destroyed() {
     // 控制遮罩
       if(this.$store.state.show){
         $(".hidshow").css({
         "display":"none"
       })
       }
}
}
</script>

<style>
*{
  padding: 0;
  margin: 0;
}
html,body{
  height: 100%;
}
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  height: 100%;
  /* color: rgb(52, 179, 136); */
}
.page {
  height: calc(100% - 52px - 44px);
  overflow-y: auto;
}
.header{
  height: 44px;
  background: #2c3e50;
}
.header h1{
  line-height: 44px;
  font-size: 16px;
  color: #ffffff;
}
ul{
  list-style: none;
}
.ul-list{
  display: flex;
  height: 52px;
  justify-content: space-between;
  align-items: flex-end;
  border-top: 1px solid rgb(134, 131, 131);
}
.ul-list li{
  width: calc(100% / 4);
}
.ul-list li:nth-child(3){
  position: relative;
}
.circle{
  position: absolute;
  right: 34%;
  bottom:5.5%;
}
.circle em{
  height: 14px;
  font-size: 10px;
  border-radius: 50%;
  background: #c00;
  color: #fff;
  line-height: 14px;
  text-align: center;
  display: inline-block;
  padding: 0 4px;
  text-indent: 0;
}
.ul-list li i{
  display: block;
  font-size: 22px;
}
.ul-list li a{
  color: #2c3e50;
  text-decoration: none;
}
.hidshow{
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: #2c3e50;
  z-index: 100;
  text-align: right;
  background: url("http://img.yipic.cn/thumb/32225003/0519ef22/f9ccb959/294aec4d/effect_big_322250030519ef22f9ccb959294aec4d.png") no-repeat;
  background-size: 100% 100%
}
.hidshow img{
  width: 100%;
  height: 100%;
}
.hidshow button{
  margin-right: 10px;
  margin-top: 10px;
  padding: 5px 10px;
  background: rgba(178, 209, 38, 0.1);
  color: #ffffff;
  border:none;
  height: 30px;
  line-height: 23px;
  box-sizing: border-box;
  text-align: center;
  border-radius: 15px;
  outline: none;
  letter-spacing: 3px;
}
.no-order{
  margin: 0 auto;
  margin-top: 150px;
}
</style>



