<template>
    <div class="newbook">
        <p>新书上架</p>
        <ul class="book-list">
            <li v-for="(item, index) in msg" :key="index" @click="Details(item.name)">
                <img :src="item.cover" alt="">
                <p class="small-p">{{item.name}}</p>
                <p class="price-p">￥{{item.price}}</p>
            </li>
        </ul>
    </div>
</template>
<script>

export default {
    name:"newbook",
    data(){
        return{
            msg:'',
            inx:""
        }
    },
     created() {
        this.$axios.get('./static/json/new-book.json', {
        params: {
            // get 参数可放置于此字段中，key-value对形式设置
            // 一般建议这么做，便于控制
        }
        }).then(res => {
       
        this.msg=res.data;
         
        })
        .catch(error => {
        console.log(error);
        })
   },
   methods:{
    Details(item){
        this.inx=item;
       this.$router.push({path:'/details',query:{goods:this.inx}});
    }

  }
}
</script>
<style scoped>

</style>





