<template>
    <div class="details">
      <keep-alive>
         <div class="deta">
             <i class="iconfont icon-Left" @click="callback"></i>
       <div class="clear">
           <img :src="books.cover" alt="">
       <div class="mess">
           <p class="over">
               <span>作者</span>
               {{books.author}}
               </p>
           <p>
               <span>页数</span>
               {{books.page}}</p>
           <p>
               <span>书号</span>
               {{books.bookNum}}</p>
           <p>
               <span class="date">出版日期</span>
               {{books.date}}
               </p>
                <p>
               <span>售价</span>
               ￥{{books.price}}
               </p>
       </div>
       </div>
       <p class="books-name">{{books.name}}</p>
       <div class="btn">
           <button class="add" @click="addshop">加入购物车</button>
           <button class="buy">立即购买</button>
       </div>
       <div class="books-text">
           <fieldset>
               <legend>书籍简介</legend>
               <p>{{books.info1}}</p>
           </fieldset>
           <fieldset>
               <legend>内容概要</legend>
               <p>{{books.info2}}</p>
           </fieldset>
           <fieldset>
               <legend>作者简介</legend>
               <p>{{books.authorInfo}}</p>
           </fieldset>
       </div>
         </div>
      </keep-alive>
    </div>
</template>
<script>
export default {
    name:"detail",
    data(){
        return{
           book:'',
           books:""
        }
    },
    created(){
        this.book=this.$route.query.goods;
        this.$axios.get('./static/json/book-info.json', {
        params: {
            // get 参数可放置于此字段中，key-value对形式设置
            // 一般建议这么做，便于控制
        }
        }).then(res => { 
            for(let i=0;i<res.data.length;i++){
                if(res.data[i].name===this.book){
                    this.books=res.data[i]  
                    return this.books;
                }
            }
            
            
        })
        .catch(error => {
        console.log(error);
        });
    },
    methods:{
        callback(){
            this.$router.go(-1);
        },
        addshop(){
            if(this.$store.state.isLogin){
                let orders = {
                "name": this.books.name,
                "price": this.books.price,
                "num": 1,
                "total":this.books.price,
                "img": this.books.cover,
                "author":this.books.author
        };
        alert("购物车添加成功");
         //实时更新购物车数量
         $(function(){
            let rootArr = JSON.parse(localStorage.orders);
            $('.circle em').text(rootArr ? rootArr.length : 0);
        })
        let rootArr = [];
        if (localStorage.orders) {
            rootArr = JSON.parse(localStorage.orders);
        }
        // 更新本地数据
        // 处理重复商品
        let flag = false;
        for (let i = 0, len = rootArr.length; i < len; i++) {
            if (rootArr[i].name === orders.name) {
                rootArr[i].num = rootArr[i].num + 1;
                 rootArr[i].total = (rootArr[i].num * rootArr[i].price).toFixed(2);
                flag = true;
                break;
            }
        }
        if (!flag) {
            rootArr.push(orders);
        }
        localStorage.orders = JSON.stringify(rootArr);
            }else{
                alert("请先登陆才能添加购物车")
            }
        }
    }
        
    
}
</script>
<style scoped>
.details{
    height: calc(100% - 52px - 44px);
    overflow-y: auto;
    text-align: left;
}
.deta{
     padding: 10px 15px;
}
.deta>i{
    position: absolute;
    z-index: 100;
    font-size: 25px;
    color: #ffffff;
    top: 7px;
    left:5px;
}
.clear>img{
    width: 100px;
    height: 120px;
}
.mess{
    vertical-align: top;
    font-size: 12px;
    margin-left: 5px;
}
.mess p{
    margin-bottom: 8px;
}
.mess span{
    margin-left: 5px;
    margin-right: 40px;
}
.mess .date{
    margin-right: 17px;
}
.clear{
    display: flex;
}
.books-name{
    overflow: hidden;
    white-space: nowrap;
    text-overflow: ellipsis;
    margin: 8px 0 10px;
}
.over{
    overflow: hidden;
    white-space: nowrap;
    text-overflow: ellipsis;
}
.btn{
    margin-bottom: 20px;
    display: flex;
    justify-content: space-between;
}
.btn button{
    padding: 10px 19px;
    border-radius: 5px;
    outline: none;
    border:none;
    border: 1px solid #000;
}
.add{
    background: #ffffff;
    
}
.btn .buy{
    padding: 11px 26px;
    border-color: #ffffff;
    color: #ffffff;
    overflow: hidden;
    background: rgb(56, 192, 68);
}
.books-text p{
    font-size: 13px;
    text-indent: 25px;
    
}
fieldset{
    margin-bottom: 15px;
    padding: 10px;
}
legend{
    padding: 0 20px 0 15px;
}
</style>





