<template>
    <div class="register login">
        <form action="#" name="myForm">
            <div class="login-text" data-msg="电话不合法">
            <i class="iconfont icon-phone"></i>
            <input type="text" placeholder="请输入你的手机号" class="phone">
        </div>
        <div class="login-text" data-msg="用户名不合法，4到16位（字母，数字，下划线，减号）">
            <i class="iconfont icon-user"></i>
            <input type="text" placeholder="请输入你的昵称" class="username">
        </div>
        <div class="login-text" data-msg="密码不合法，4到16位（字母，数字，下划线，减号）">
            <i class="iconfont icon-password"></i>
            <input type="password" placeholder="请输入你的密码" class="password">
        </div>
        <div class="login-text" data-msg="邮箱格式不正确">
            <i class="iconfont icon-password1"></i>
            <input type="email" placeholder="请输入你的邮箱" class="email">
        </div>
        <button class="login-btn" @click="loginBtn">注册</button>
        </form>
    </div>
</template>
<script>
    export default{
        name:"register",
        data(){
            return{}
        },
        methods:{
        loginBtn(){
            setTimeout(() => {
            if(sessionStorage.isLogin){
                this.$router.push({path:'/person-center'});
            }
        }, 1000);
            }
        },
        mounted(){
            let _isLogin = false;
        // 3. 表单验证
        $('.login-text input').on('input', function () {
            let type = this.className;
            if (Validate.test(type, $(this).val())) {
                // 合法
                $(this).parent().removeClass('error');
            } else {
                // 不合法
                $(this).parent().addClass('error');
            }
        });
        // 4. 点击登陆注册按钮
        $('.login-btn').on('click', function () {
            let isThrough = true;
            let $inputs =  _isLogin ? $(".login-text:lt(2) input") : $(".login-text input") ;
            // 表单异常处理
            for (let i = 0, len = $inputs.length; i < len; i++) {
                if (!$inputs.eq(i).val() || $inputs.eq(i).parent().hasClass('error')) {
                    isThrough = false;
                    break;
                }
            }
            if (!isThrough) {
               alert("信息为空或信息不合法！");
               return;
            }
            // 注册Bmob服务
            Bmob.initialize("44d02198809694a29dca95912ce066eb", "149f5b3499ab2f3357eb27bf42d5a899");
            if (_isLogin) {
                // 登陆
                Bmob.User.login($('.username').val(),$('.password').val()).then(res => {
                   sessionStorage.isLogin = true;
                    alert("登陆成功");
                }).catch(err => {
                   alert(err.error);
                });
            } else {
                // 创建用户对象
                let user = {};
                let date=new Date();
               
                $inputs.each((index, input) => {
                    let key = $(input).attr('class');
                    let val = $(input).val();
                    user.time=date.toLocaleString();
                    user[key] = val;
                });
                // 调用BMOB注册用户
                Bmob.User.register(user).then(res => {
                    sessionStorage.isLogin = true;
                    let data=JSON.parse( localStorage.bmob);
                    date={};
                    data=user;
                    localStorage.bmob=JSON.stringify(data)
                     alert("注册成功");
                }).catch(err => {
                    alert(err.error);
                });
            }
        });
class Validate {
    constructor() {}
    static test(type, str) {
        let res = null;
        switch (type) {
            case "username": {
                // 1. 用户名正则，4到16位（字母，数字，下划线，减号）
                res = /^[A-Za-z0-9_-]{4,16}$/.test(str);
            } break;
            case "password": {
                // 2. 用户名正则，4到16位（字母，数字，下划线，减号）
                res = /^[A-Za-z0-9_-]{4,16}$/.test(str);
            } break;
            case "email": {
                // 3. 邮箱
                res = /^([A-Za-z0-9_\-\.])+\@([A-Za-z0-9_\-\.])+\.([A-Za-z]{2,4})$/.test(str);
            } break;
            case "phone": {
                // 4. 手机 
                res = /^((13[0-9])|(14[5|7])|(15([0-3]|[5-9]))|(18[0-4,5-9]))\d{8}$/.test(str);
            } break;
        }
        return res;
    }
 }
 }

    }
</script>
<style scoped>

</style>




