// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from 'vue'
import App from './App'
import router from './router'
import store from './store'
// 导入axios
import Axios from 'axios'
// 将axios挂载到Vue上
Vue.prototype.$axios = Axios
// 引入swiper
import VueAwesomeSwiper from 'vue-awesome-swiper'
import 'swiper/dist/css/swiper.css'
Vue.use(VueAwesomeSwiper);


Vue.config.productionTip = false
router.beforeEach((to,from,next) =>{
  switch(to.name){
    case "home": store.commit("changeTitle","首页");break;
    case "classify": store.commit("changeTitle","分类");break;
    case "shop": store.commit("changeTitle","购物车");break;
    case "login": store.commit("changeTitle","登陆/注册");break;
    case "detail": store.commit("changeTitle","图书详情");break;
    case "personCenter": store.commit("changeTitle","个人中心");break;
    case "personData": store.commit("changeTitle","个人资料修改");break;
    case "settlement": store.commit("changeTitle","结算中心");break;
  };
  // 提交登陆状态
  if(sessionStorage.isLogin){
    store.commit('changeLoginState', true);
    let name=JSON.parse(localStorage.bmob)
    store.commit('changename',name);
  }else{
    store.commit('changeLoginState', false);
  }
  next()
})
/* eslint-disable no-new */
new Vue({
  el: '#app',
  router,
  store,
  components: { App },
  template: '<App/>'
})



