<template>
    <div class="home page">
      <div class="swiper-container">
    <div class="swiper-wrapper">
      <!-- <div class="swiper-slide" v-for="(item, index) in items" :key="index"></div> -->
      <div class="swiper-slide" v-for="(item, index) in imgs" :key="index"><img :src="item" alt="" /></div>
    </div>
    <!-- 如果需要分页器 -->
    <div class="swiper-pagination"></div>
  </div>
  <div class="text">
      <i class="iconfont icon-bell"></i>
      <span>双12全场图书低至1折，跟有隐藏神秘惊喜</span>
  </div>
  <newbook/>
  <edit/>
  <hotbook/>
    </div>
</template>
<script>
import Swiper from 'swiper'
import 'swiper/dist/css/swiper.min.css'
// 引入新书
import newbook from '../components/newbook' 
// 引入编辑推荐
import edit from '../components/edit' 
// 引入热门
import hotbook from '../components/hotbook' 
export default {
    name:"home",
    data(){
        return{
            swiper:null,
            imgs:[
                "http://img.zcool.cn/community/01a20c5596322b32f87598b53a4b2e.jpg@3000w_1l_2o_100sh.jpg",
                "http://img.zcool.cn/community/01657f5a098c2ca801204a0e67d220.gif",
                "http://www.deyu.ln.cn/images/nfwwcz3ffzzgkylefzshk33lmfxc4y3pnu/mfsv2/download/s010/p01xoKD00jUo/5ZWf5QivgejFQe.jpg",
                "http://img.zcool.cn/community/0162fd598abd1c000000212940b1e6.jpg"
            ]
        }
    },
    components:{
        newbook,
        edit,
        hotbook
    },
    mounted() {
	  this.swiper= new Swiper('.swiper-container', {
      direction: 'horizontal',
      autoplay: true,
      autoplayDisableOnInteraction: false,
      loop: true,
      pagination: {
      el: '.swiper-pagination',
      clickable :true,
    },
      paginationClickable: true
    })
    }
    
}
</script>
<style >
.swiper-slide img{
    width: 100%;
    height: 200px;
}
.text{
    padding: 8px 10px 10px;
    border-bottom: 1px solid #d3d3d3;
    box-sizing: border-box;
}
.text i{
    color:yellow;
    font-size: 20px;
}
.text span{
    font-size: 14px;
}
.newbook{
    padding: 15px;
    padding-bottom: 0px;
}
.newbook>p{
    text-align: left;
    font-weight: bolder;
    color: rgb(136, 191, 212);
    margin-bottom: 10px;
}
.book-list{
    display: flex;
    flex-wrap: wrap;
}
.book-list li{
    width: 47%;
    justify-content: space-between;
    align-content: space-between;
    margin-bottom: 10px;
}
.book-list li:nth-child(2n-1){
    margin-right: 15px;
}
.book-list li img{
    width: 140px;
    height: 190px;
}
.book-list li p{
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
}
.small-p{
    font-size: 14px;
    color: rgb(129, 158, 158);
    margin-bottom: 5px;
    margin-top: 8px;
}
.price-p{
    font-size: 13px;
    text-align: left;
    color: rgb(218, 98, 62);
}
</style>





