import Vue from 'vue'
import Router from 'vue-router'
import home from '../pages/home';
import classify from '../pages/classify';
import mine from '../pages/mine-center';
import shop from '../pages/shop-cart';
import detail from '../components/details';
import login from '../pages/login';
import register from '../pages/register';
import personCenter from '../pages/person-center';
import personData from '../pages/person-data';
import settlement from '../pages/settlement';
import store from '../store'
Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'home',
      component:home
    },
    {
      path: '/classify',
      name: 'classify',
      component:classify
    },
    {
      path: '/mine-center',
      name: 'mine',
      component:mine,
      redirect: to =>{  
          if(!store.state.isLogin){
            return '/mine-center/login'
          }else{
            return '/person-center'
          }
      },
      children:[
        {
        path: '/mine-center/login',
        name: 'login',
        component:login
      },
      {
        path: '/mine-center/register',
        name: 'register',
        component:register
      }
    ]
    },
    {
      path: '/shop-cart',
      name: 'shop',
      component:shop
    },
    {
      path: '/details',
      name: 'detail',
      component:detail
    },
    {
      path: '/person-center',
      name: 'personCenter',
      component:personCenter
    },
    {
      path: '/person-data',
      name: 'personData',
      component:personData
    },
    {
      path: '/settlement',
      name: 'settlement',
      component:settlement
    }
  ]
})



