<template>
    <div class="person-name page">
        <i class="iconfont icon-Left" @click="callback" ref="showi"></i>
        <ul class="person-name-list">
            <li>
                <label>昵称</label>
               <input type="text" :value="this.$store.state.tagName.username">
            </li>
            <li>
                <label>E-mail</label>
                <input type="text" :value="this.$store.state.tagName.email">
            </li>
            <li>
                <label>注册日期</label>
                <span>{{this.$store.state.tagName.time}}</span>
            </li>
            <li>
                <label>手机号码</label>
                <input type="text" :value="this.$store.state.tagName.phone">
            </li>
            <li>
                <label>个人描述</label>
                <input type="text" value="这人很懒，什么都没有留下" class="disabel">
            </li>
        </ul>
        <div class="ed-btn"><button>确认修改</button></div>
    </div>
</template>
<script>
export default {
    name:"person-name",
    data(){
        return{
           
        }
    },
    methods:{
        callback(){
            this.$router.go(-1);
        }
    },
    mounted(){
        
    }
}
</script>
<style scoped>
.person-name>i{
    position: absolute;
    z-index: 100;
    font-size: 25px;
    color: #ffffff;
    top: 7px;
    left:5px;
}
.person-name-list li{
    padding:12px 10px;
    text-align: left;
    font-size: 14px;
    border-bottom: 1px solid #d3d3d3;
}
.person-name-list label{
    color: #797979;
    width: 56px;
    display: inline-block;
}
.person-name-list span{
    margin-left: 50px;
    color: #5a8d94;
}
.person-name-list input{
    color: #5a8d94;
    margin-left: 50px;
    border:none;
    outline: none;
}
.ed-btn{
    padding: 10px;
}
.ed-btn button{
    padding: 10px 0;
    width: 100%;
    background: #359A35;
    border: none;
    outline: none;
    color: #ffffff;
    letter-spacing: 5px;
    border-radius: 5px;
    margin-top: 40px;
}
</style>





