<template>
    <div class="mine page">
        <div class="mine-page">
            <div class="loer" ref="loer">
           <span> <router-link to='/mine-center/login' class="go-login">登陆</router-link></span>
            <span><router-link to='/mine-center/register' class="go-register">注册</router-link></span>
            </div>
            <router-view></router-view>
        </div>
    </div>
</template>
<script>
export default {
    name:"mine",
    data(){
        return{
           
        }
    },
    created(){
        // if(this.$store.state.isLogin){
        //     this.$router.push({path:'/person-center'});
        // }
         
    }
}
</script>
<style >
.mine-page{
    padding: 10px;
}
.loer{
    padding: 5px 10px 0px;
    display: flex;
    justify-content: space-around;
    border-bottom: 1px solid #d3d3d3;
}
.loer span{
    padding: 0 15px 10px;
    letter-spacing: 5px;
    /* border-bottom: 3px solid #2da30f */
}
.loer span a{
    text-decoration: none;
    color: #000;
}
.login{
    padding: 20px 0;
}
.login .login-text input{
    width: 100%;
    padding: 10px 0 5px;
    padding-left: 35px;
    box-sizing: border-box;
    border: none;
    outline: none;
}
.login-text{
    padding:12px 10px;
    position: relative;
    border-bottom: 1px solid #d3d3d3;
}
.login-text i{
    position: absolute;
    top:17px;
    font-size: 20px;
}
.login-btn{
    padding: 12px;
    width: 100%;
    margin-top: 30px;
    border: none;
    outline: none;
    letter-spacing: 5px;
    color: #ffffff;
    border-radius: 5px;
    background: #339933;
}
.loer .router-link-active{
    color: #339933;
}
.login .login-text::after{
    display: none;
    content: attr(data-msg);
    font-size: 12px;
    color: red;
    position: absolute;
    top: 72%;
    left: 0;
}
.login .error::after {
    display: block;
  }
</style>





