<template>
    <div class="settlement page">
        <i class="iconfont icon-Left" @click="callback" ref="showi"></i>
       <div class="pad">
            <h1>商品信息确认</h1>
            <div class="shop shop-num">
                <h2>价格信息</h2>
                <p>
                    <span>商品总价：</span>
                    <label>￥{{price?price:0}}</label>
                </p>
                <p>
                    <span>运送费：</span>
                    <label>以免邮费</label>
                </p>
                <p>
                    <span>订单总计：</span>
                    <label>￥{{price?price:0}}</label>
                </p>
            </div>
             <div class="shop">
                <h2>收货信息</h2>
                <p>
                    <span>收货地址：</span>
                    <label>四川省 成都市 高新区 锦城大道东段</label>
                </p>
                <p>
                    <span>收货人：</span>
                    <label>{{this.$store.state.tagName.username?this.$store.state.tagName.username:"暂无,请先登陆"}}</label>
                </p>
                <p>
                    <span>邮编：</span>
                    <label>610000</label>
                </p>
                <p>
                    <span>电话：</span>
                    <label>{{this.$store.state.tagName.phone?this.$store.state.tagName.phone:"暂无"}}</label>
                </p>
            </div>
            <div class="edit">
                修改地址
                 <i class="iconfont icon-right"></i>
            </div>
            <div class="edit new">
                新增地址
                 <i class="iconfont icon-right"></i>
            </div>
             <h1>选择支付方式</h1>
             <ul class="mode">
                 <li>
                      <i class="iconfont icon-h_icon_weixn"></i>
                     <span>微信支付</span>
                     <i class="iconfont icon-right"></i>
                 </li>
                 <li>
                     <i class="iconfont icon-zhifubao"></i>
                      <span>支付宝</span>
                      <i class="iconfont icon-right"></i>
                 </li>
                 <li>
                     <i class="iconfont icon-yinghangka01"></i>
                      <span>银行卡</span>
                      <i class="iconfont icon-right"></i>
                 </li>
                 <li>
                     <i class="iconfont icon-xinyongqia"></i>
                      <span>信用卡</span>
                      <i class="iconfont icon-right"></i>
                 </li>
       </ul>
       </div> 
    </div>
</template>
<script>
export default {
    name:"settlement",
    data(){
        return{
           price:""
        }
    },
    mounted(){
        this.price=JSON.parse(sessionStorage.price)
    },
    methods:{
        callback(){
            this.$router.go(-1);
        }
    }
}
</script>
<style scoped>
.settlement>i{
    position: absolute;
    z-index: 100;
    font-size: 25px;
    color: #ffffff;
    top: 7px;
    left:5px;
}
.settlement{
    background: #F2F2F2;
}
.settlement h1{
    color: #6B6B6B;
    font-size: 18px;
    text-align: left;
    margin-bottom: 15px;
}
h2{
    text-align: left;
    font-size: 17px;
}
.pad{
    padding: 10px;
}
.shop-num label{
    float: right;
}
.shop{
    padding: 10px;
    background: #ffffff;
    border-block-end: 1px solid #d3d3d3;
}
.shop p{
    text-align: left;
    margin-top: 5px;
}
.shop p span,.shop p label{
    color: #A58696;
    font-size: 13px;
}
.shop p span{
    display: inline-block;
    width: 65px;
}
.edit{
    text-align: left;
    background: #ffffff;
    padding: 10px;
    font-size: 15px;
    border-bottom: 1px solid #d3d3d3;
}
.new{
    margin-bottom: 10px;
}
.mode{
    text-align: left;
    background: #ffffff;
    border-block-end: 1px solid #d3d3d3;
}
.mode li{
    padding: 10px;
    border-bottom: 1px solid #d3d3d3;
}
.mode li i:first-child{
    color: rgb(184, 70, 129);
    font-size: 20px;
    margin-right: 5px;
}
.icon-right{
    float: right;
}
</style>





